$("._toggle-mob-menu").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$("._show-tag-menu").click(function () {
    $(".header").removeClass("_open")
    $(".header").addClass("_open-tag-menu")
})

$("._hide-tag-menu").click(function () {
    $(".header").removeClass("_open-tag-menu")
    $("body").removeClass("fixed-body")
})

$(".search-toggle").click(function () {
    $(".header").toggleClass("_open-search")
})
