$('.file-input input').change(function(){
    const file = this.files[0];
    let self = $(this)
    if (file){
        let reader = new FileReader();
        reader.onload = function(event){
            $(self).parents(".file-input").find(".file-input__preview img").attr('src', event.target.result);
            $(self).parents(".file-input").addClass("_uploaded")
        }
        reader.readAsDataURL(file);
    }
});
